import uniNumberBox from '@/components/uni-number-box.vue';
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import api from '@/common/index.js';
import over from '@/components/over.vue';
import imglazy from '@/components/img-lazy/img-lazy.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
var eindex = {
	flag: 0,
	mindex: -1,
	lastX: 0,
	lastY: 0,
	text: 0,
	navIndex: 0,
	typeList: [],
	activeId: "",
	downNum: '',
	cartdata: [],
	activeClass: 0,
	cartList: [],
	scrollTop: 0,
	total: 0, //总价格
	allCount: [],
	navList: [],
	navListRight: [],
	typeId: "",
	windowHeight: 0,

	spnum: 0,
	showRigth: false,
	showLeft: false,
	activeList: [],
	disnum: 0,
	distext: '请选择代金券',
	filterIndex: 0,
	priceOrder: 0, //1 价格从低到高 2价格从高到低
	aniClass: '-1',
	page: 1,
	pageSize: 20,
	type: 'add',
	isShow: false,
	navActive: 1,
	listId: -1,
	navStatus: false,
	onPull: 1,
	loadingType: 'more', //加载更多状态
	sptyp: ''
}
export default {
	components: {
		uniNumberBox,
		uniDrawer,
		over,
		imglazy,
		uniLoadMore,
		uniNavBar
	},
	data() {
		return {

			list: {
				flag: 0,
				mindex: -1,
				lastX: 0,
				lastY: 0,
				text: 0,
				navIndex: 0,
				typeList: [],
				activeId: "",
				downNum: '',
				cartdata: [],
				activeClass: 0,
				cartList: [],
				scrollTop: 0,
				total: 0, //总价格
				loadingType: 'more', //加载更多状态
				page: 1,
				pageSize: 20,
				type: 'add',
				allCount: [],
				navList: [],
				navListRight: [],
				typeId: "",
				windowHeight: 0,
				sptyp: '',
				spnum: 0,
				showRigth: false,
				showLeft: false,
				activeList: [],
				disnum: 0,
				distext: '请选择代金券',
				filterIndex: 0,
				priceOrder: 0, //1 价格从低到高 2价格从高到低
				aniClass: '-1',
				isShow: false,
				navActive: 1,
				listId: -1,
				navStatus: false,
				onPull: 1,
			}
		};
	},
	onLoad() {

	},
	onShow() {
		let that = this
		const params = uni.getStorageSync('userinfo');
		if (!params) {
			this.$store.dispatch('showLogin');
			return false;
		}
		api.load(this.$t('index').loadmsg)
		setTimeout(function() {
			eindex.page = 1;
			eindex.onPull = 1;
			eindex = that.list;
			eindex.navList = [];
			eindex.navListRight = [];
			eindex.cartList = [];
			eindex.empty = false;
			that.list = eindex;
			eindex.loadingType = "more";
			that.distext = that.$t('cart').select;
			that.loadData();
		}, 400)
	},

	computed: {
		i18n() {
			return this.$t('cart');
		}
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			eindex.headerPosition = "fixed";
		} else {
			eindex.headerPosition = "absolute";
		}
	},

	//下拉刷新
	onPullDownRefresh() {
		eindex.onPull = 1;
		eindex.page = 1;
		eindex.type = 'refresh';
		this.loadData();
	},
	//加载更多
	onReachBottom() {
		if (eindex.onPull == 1) {
			eindex.type = 'add';
			this.loadData();
		}

	},
	methods: {
		// 分类
		handletouchmove: function(event) {
			// console.log("开始移动")
			if (eindex.flag !== 0) {
				return;
			}
			let currentX = event.touches[0].pageX;
			let currentY = event.touches[0].pageY;
			let tx = currentX - eindex.lastX;
			let ty = currentY - eindex.lastY;
			let text = '';
			eindex.mindex = -1;
			//左右方向滑动
			if (Math.abs(tx) > Math.abs(ty)) {
				if (tx < 0) {
					text = '向左滑动';
					eindex.flag = 1;
					this.hide();
					// this.getListleft(); //调用列表的方法
				} else if (tx > 0) {
					text = '向右滑动';
					eindex.flag = 2;
					this.show("left");
					// this.getListright(); //调用列表的方法
				}
			}
			//上下方向滑动
			else {
				if (ty < 0) {
					text = '向上滑动';
					eindex.flag = 3;
					//  this.getList();  //调用列表的方法
				} else if (ty > 0) {
					text = '向下滑动';
					eindex.flag = 4;
				}
			}
			//将当前坐标进行保存以进行下一次计算
			eindex.lastX = currentX;
			eindex.lastY = currentY;
			eindex.text = text;

		},
		handletouchstart: function(event) {
			// console.log("开始触摸")
			eindex.lastX = event.touches[0].pageX;
			eindex.lastY = event.touches[0].pageY;
		},
		handletouchend: function(event) {
			// console.log("结束")
			eindex.flag = 0;
			eindex.text = '没有滑动';
		},
		loadStatus(item) {
			// console.log(item)
			eindex.total = item.amt;

			let list = eindex.cartList;

			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			eindex.loadingType = 'nomore';
		},
		// 商品分类点击
		changeCate(item, index) {
			let that = this;
			eindex.page = 1;
			eindex.navStatus = true;
			eindex.onPull = 2;
			// 父类
			if (item.cls == "cn") {
				let childList = item.cn
				childList.forEach(function(item, index) {
					item.checked = true;
					item.image = that.$store.getters.imgShow + '/thumb/' + item.item_no + '.jpg?x-oss-process=style/80';
					item.allNum = parseInt(item.spec);
					item.num = parseInt(item.num)
				});

				eindex.cartList = childList;
				eindex.listId = item.cls;
				that.loadStatus(item);
				that.hide();
				return false;
			}
			if (item.cls == "zhe") {
				let childList = item.zhe
				childList.forEach(function(item, index) {
					item.checked = true;
					item.image = that.$store.getters.imgShow + '/thumb/' + item.item_no + '.jpg?x-oss-process=style/80';
					item.allNum = parseInt(item.spec);
					item.num = parseInt(item.num)
				});
				eindex.cartList = childList;
				eindex.listId = item.cls;
				that.loadStatus(item);
				that.hide();
				return false;
			}
			if (eindex.navActive == 1) {
				let childList = item.goods
				childList.forEach(function(item, index) {
					item.checked = true;
					item.image = that.$store.getters.imgShow + '/thumb/' + item.item_no + '.jpg?x-oss-process=style/80';
					item.allNum = parseInt(item.spec);
					item.num = parseInt(item.num)
				});
				eindex.cartList = childList;
				eindex.listId = item.cls;
				if (item.child != undefined) {
					if (item.child) {
						eindex.navActive = 2;
					}
					eindex.navIndex = index;
				}
				that.loadStatus(item);
				that.hide();
			} else {
				// 子类
				let childList = item.goods
				childList.forEach(function(item, index) {
					item.checked = true;
					item.image = that.$store.getters.imgShow + '/thumb/' + item.item_no + '.jpg?x-oss-process=style/80';
					item.allNum = parseInt(item.spec);
					item.num = parseInt(item.num)
				});
				eindex.cartList = childList
				eindex.listId = item.clss;
				if (item.child == undefined) {
					if (item.child) {
						eindex.navActive = 2;
					}
					// eindex.navIndex = index;
				}
				that.loadStatus(item);
				that.hide();
			}


		},
		upper() {
			eindex.navActive = 1;
			eindex.activeId = -1;
			this.list = eindex;
		},
		// 管理
		actionSheetTap() {
			let that = this;
			uni.showActionSheet({
				title: that.$t('cart').regulate,
				itemList: that.$t('cart').sheet,
				cancelText: '取消',
				success: (e) => {
					let i = e.tapIndex;
					eindex.listId = -1;
					eindex.navActive = 1
					if (i == 0) {
						eindex.page = 1;
						eindex.onPull = 1;
						eindex.cartList = [];
						eindex.downNum = "desc"
						that.loadData();
						api.load(that.$t('index').loadmsg)
					}
					if (i == 1) {
						eindex.page = 1;
						eindex.onPull = 1;
						eindex.cartList = [];
						eindex.downNum = "asc"
						that.loadData();
						api.load(that.$t('index').loadmsg)
					}
					if (i == 2) {
						that.clearCart();
					}
					that.list = eindex;
				}
			})
		},

		//请求数据
		loadData() {

			if (eindex.loadingType != "nomore") {
				eindex.empty = false;
				eindex.over = false;
				eindex.activeClass = 0;
				let that = this;
				let params = {
					downNum: eindex.downNum,
					page: eindex.page
				}
				that.$ajax.cartData(params).then(res => {
					that.allPage(res);
				})
			}

		},
		// 分页
		allPage(res) {
			let that = this;
			eindex.cartdata = res;
			let cartList = res.list; //商品列表
			let navListRight = res.hd_list; //分类
			let navList = res.cate;
			let classifyList = [] // 创建商品分类列表

			// 当商品为空时
			if (res.other.totalpage == 0) {
				eindex.loadingType = "nomore";
				eindex.empty = true;
				eindex.cartList = [];
				eindex.navList = [];
				eindex.total = 0;
				eindex.disnum = 0;
				eindex.spnum = 0;
				eindex.sptyp = 0;
				eindex.distext = that.$t('cart').select;
				let arry = '';
				uni.setStorageSync('getlist', arry);
				that.list = eindex;
				uni.hideLoading();
				return false;
			}

			// 添加图片
			eindex.spnum = 0;
			eindex.sptyp = res.sptyp;
			cartList.forEach(function(item, index) {
				item.checked = true;
				item.image = that.$store.getters.imgShow + '/thumb/' + item.item_no + '.jpg?x-oss-process=style/80';
				item.allNum = parseInt(item.spec);
				item.num = parseInt(item.num);
				eindex.spnum = eindex.spnum + item.num
			});
			// 加载状态
			eindex.loadingType = parseInt(res.other.page) > parseInt(res.other.totalpage) ? 'nomore' : 'more';

			//请求分页
			if (eindex.type == 'add') {
				eindex.page = eindex.page + 1;

				if (eindex.loadingType === 'nomore') {
					that.list = eindex;
					return;
				}
				eindex.loadingType = 'loading';
			} else {
				eindex.loadingType = 'more'
			}

			// 刷新页面
			if (eindex.type == 'refresh') {
				eindex.cartList = [];
				uni.stopPullDownRefresh();
				eindex.page = eindex.page + 1;

				if (eindex.loadingType === 'nomore') {
					that.list = eindex;
					return;
				}
				eindex.loadingType = 'loading';
			}
			// 当前页面小于20条
			if (cartList.length < 20) {
				eindex.loadingType = 'nomore';
			}
			// 导航列表
			navListRight.forEach(function(item, index) {
				item.title = item.huodong_name;
				item.cls = item.huodong;
				navList.push(item)
			})

			// if (classifyList.length == 0) {
			// 	eindex.cartList = cartList; //全部商品
			// } else {
			// 	eindex.cartList = classifyList; //分类商品
			// }

			eindex.cartList = eindex.cartList.concat(cartList);
			eindex.over = true;
			eindex.typeList = res.cate;
			if (res.other.page == 1) {


				eindex.total = res.spprice;
			}
			// console.log(eindex.cartList)
			uni.setStorageSync('getlist', cartList);
			// that.calcTotal(); //计算总价
			// that.disData();
			uni.hideLoading();
			that.list = eindex
		},
		//详情页
		navToDetailPage(item) {
			console.log(item)
			if (item.huodong == "zhe") {
				uni.navigateTo({
					url: `/pages/product/product?productId=${item.item_no}&num=${item.price}`
				})
			} else {
				uni.navigateTo({
					url: `/pages/product/product?productId=${item.item_no}`
				});
			}

		},
		// 商品分类
		changenav(item, index) {
			let list = uni.getStorageSync('getlist');
			let typeId = item.cls;
			eindex.typeId = item.cls;

			eindex.activeClass = index;
			if (typeId == "默认") {
				eindex.cartList = list;

			} else {
				let navItem = [];
				list.forEach(function(data, j) {
					if (parseInt(typeId) >= 0) {
						if (data.cls_id === typeId) {
							navItem.push(data)
						}
					} else {
						if (data.huodong == typeId) {
							navItem.push(data)
						}
					}
				})
				eindex.cartList = navItem;

			}
			this.list = eindex;
			// this.calcTotal(item)
		},
		//优惠券选中状态处理
		discheck(type, index) {
			// let disList = eindex.activeList;
			// // 当优惠券有多张时
			// let that = this;
